<template>
  <div class="dialogBox">
    <el-form
      :model="formData"
      :rules="rules"
      size="medium"
      ref="formData"
      label-width="85px"
      label-position="right"
    >
      <el-form-item label="" prop="name">
        <el-input v-model="formData.name" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="formData.name" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="formData.name" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="formData.name" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="" prop="region">
        <el-select v-model="formData.region" placeholder="" clearable>
          <el-option label="" value="shanghai"></el-option>
          <el-option label="" value="beijing"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="desc">
        <el-input
          type="textarea"
          resize="none"
          style="width:400px;"
          :autosize="{ minRows: 4, maxRows: 6}"
          v-model="formData.desc"
          placeholder=""
        ></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer ac">
      <el-button type="primary" @click="submitForm('formData')"> </el-button>
      <el-button @click="cancelForm"> </el-button>
    </div>
  </div>
</template>
<script>
export default {
  name: "areaEdit",
  data() {
    return {
      ruleArr: [{ name1: "", name2: "" }],
      // 
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        region: [
          { required: true, message: "", trigger: "change" }
        ],
        store: [
          { required: true, message: "", trigger: "change" }
        ],
        time: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      }
    };
  },
  props: {
    formData: {
      type: Object,
      default: function() {
        return {
          type: "",
          name: "",
          time: "",
          buy: "",
          give: "",
          ruleArr: "",
          type: "",
          resource: "",
          desc: "",
          time: ""
        };
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    typeRadio(e) {
      console.log(e);
    },
    buyRadio(e) {
      console.log(e);
    },
    giveRadio(e) {
      console.log(e);
    },
    addRuls() {
      this.ruleArr.push({ name1: "", name2: "" });
    },
    delRuls(e) {
      if (this.ruleArr.length > 1) {
        this.ruleArr.splice(e, 1);
      }
    },
    // 
    init() {},
    // 
    cascaderChange(val) {
      console.log(val);
    },
    // 
    sucImgB(res, file) {
      console.log(res);
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeImgB(file) {
      console.log(file);
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(" JPG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    },
    // 
    sucImgS() {},
    beforeImgS() {},
    remImgS(file, fileList) {
      console.log(file, fileList);
    },
    // 
    treeClick(e) {
      console.log(this.$refs.tree.getCheckedKeys());
    },

    // 
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.formData);
        console.log(valid);
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });

      this.$emit("submitForm");
    },
    // 
    cancelForm() {
      this.$emit("cancelForm");
    }
  }
};
</script>
<style lang="scss" scoped>
.el-dialog .el-dialog__body .avatar-uploader {
  position: relative;
  .avatar-uploader-icon {
    width: 150px;
    height: 150px;
    text-align: center;
    font-size: 25px;
    line-height: 120px;
  }
  .el-upload__tip {
    position: absolute;
    top: 10px;
    left: 170px;
    width: 240px;
  }
}
</style>




