<template>
  <div class="dialogBoxTab">
    <el-tabs type="border-card">
      <el-tab-pane label="">
        <el-row v-for="(item,index) in userInfo" :key="index">
          <el-col :span="2" class="ac">
            <div>{{item.name}}</div>
          </el-col>
          <el-col :span="1" class="ac">
            <div>:</div>
          </el-col>
          <el-col :span="6" class="al">
            <div>
              {{dataList[item.value]}}
              <i
                class="el-icon-edit phoneEdit"
                v-if="item.modify"
                @click="openPhone(dataList[item.value])"
              ></i>
            </div>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="">
        <div class="titleText">
           :
          <span></span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
           :
          <span></span>
        </div>
        <div class="mainTable_content">
          <table-base
            :tableIndex="tableIndex"
            :tableHead="tableHead"
            :tableData="tableData"
            :tableSize="tableSize"
            :tableTotal="tableTotal"
            @sizeChangeEvent="sizeChangeEvent"
            @currentChangeEvent="currentChangeEvent"
          ></table-base>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
export default {
  name: "userEdit",
  data() {
    return {
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,

      userInfo: [
        { name: "", value: "name" },
        { name: "", value: "sex" },
        { name: "", value: "birth" },
        { name: "", value: "duties" },
        { name: "", value: "mail" },
        { name: "", value: "data" },
        { name: "", value: "phone", modify: true }
      ],

      dataList: {
        name: "",
        sex: "",
        birth: "1991.10.8",
        duties: "",
        mail: "243326928@qq.com",
        data: "2018.10.08",
        phone: "18929300245"
      },

      tableHead: [
        { name: "", value: "name" },
        { name: "", value: "date" },
        { name: "", value: "address" },
        { name: "", value: "progress" },
        { name: "", value: "id" },
        { name: "", value: "status" }
      ],
      tableData: [
        {
          id: 1,
          date: "2016-05-02",
          name: "",
          status: "1",
          address: " 1518 ",
          progress: 10
        },
        {
          id: 2,
          date: "2016-05-04",
          name: "",
          status: "2",
          address: " 1517 ",
          progress: 30
        },
        {
          id: 3,
          date: "2016-05-01",
          name: "",
          status: "1",
          address: " 1519 ",
          progress: 60
        },
        {
          id: 4,
          date: "2016-05-03",
          name: "",
          status: "1",
          address: " 1516 ",
          progress: 90
        },
        {
          id: 5,
          date: "2016-05-03",
          name: "",
          status: "2",
          address: " 1516 ",
          progress: 100
        }
      ]
    };
  },
  // 
  components: {
  },
  props: {
    formData: {
      type: Object,
      default: function() {
        return {
          type: "",
          name: "",
          time: "",
          buy: "",
          give: "",
          ruleArr: "",
          type: "",
          resource: "",
          desc: "",
          time: ""
        };
      }
    }
  },
  created() {
    this.init();
    console.log(this.$root.fathData)
  },
  methods: {
    // 
    init() {
      console.log(this.formData);
    },
    // 
    openPhone() {
      this.$prompt(
        // ", . !",
        "",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          // inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
          inputErrorMessage: "",
          inputPlaceholder: ""
        }
      )
        .then(({ value }) => {
          this.$message({
            type: "success",
            message: ": " + value
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    sizeChangeEvent(e) {
      console.log(` ${e} `);
    },
    currentChangeEvent(e) {
      console.log(`: ${e}`);
    }
  }
};
</script>
<style lang="scss" scoped>
.el-row {
  font-size: 16px;
  line-height: 40px;
  .phoneEdit {
    margin-left: 10px;
    cursor: pointer;
    font-size: 18px;
  }
}
.titleText {
  font-size: 18px;
  font-weight: bold;
}
</style>




