<template>
  <div class="dialogBox sizeInline">
    <el-form
      inline
      :model="formData"
      :rules="rules"
      size="medium"
      ref="formData"
      label-width="85px"
      label-position="right"
    >
      <el-form-item label="" prop="name">
        <el-input v-model="formData.name" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="" prop="time">
        <el-date-picker
          clearable
          v-model="formData.time"
          type="datetimerange"
          align="right"
          range-separator=""
          start-placeholder=""
          end-placeholder=""
        ></el-date-picker>
      </el-form-item>
      <!-- <el-form-item label="" prop="type">
        <el-radio-group v-model="formData.type" @change="typeRadio">
          <el-radio label="money"></el-radio>
          <el-radio label="card"></el-radio>
        </el-radio-group>
      </el-form-item>-->
      <el-form-item label="">
        <el-radio-group v-model="formData.buy" @change="buyRadio">
          <el-radio style="line-height: 36px;" label="firstbuy"> ()</el-radio>
          <el-radio style="line-height: 36px;" label="nofirstbuy"> (, )</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="">
        <el-radio-group v-model="formData.give" @change="giveRadio">
          <el-radio style="line-height: 36px;" label="unite"> (: 10010)</el-radio>
          <el-radio style="line-height: 36px;" label="ladder"> (: 1005, 20015)</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="name">
        <ul>
          <li class="ruleBox" v-for="(item, index) in ruleArr" :key="index">
            <el-input class="wid160 ac" v-model="item.name1" placeholder="" clearable></el-input>&nbsp;  &nbsp; , &nbsp;  &nbsp;
            <el-input class="wid160 ac" v-model="item.name2" placeholder="" clearable></el-input>&nbsp; 
            <i class="el-icon-circle-plus font30 vm ml10 cupo hide" @click="addRuls"></i>
            <i class="el-icon-remove font30 vm ml10 cupo" @click="delRuls(index)"></i>
          </li>
        </ul>
        <!-- <ul>
          <li class="ruleBox" v-for="(item, index) in ruleArr" :key="index">
            <el-input class="wid160 ac" v-model="item.name1" placeholder="" clearable></el-input>&nbsp;  &nbsp; , &nbsp;  &nbsp;
            <span>0</span>&nbsp;  &nbsp;
            <el-button
              type="primary"
              size="mini"
              style="padding:10px 15px"
              @click="dialogCardList = true"
            ></el-button>
            <i class="el-icon-circle-plus font30 vm ml10 cupo hide" @click="addRuls"></i>
            <i class="el-icon-remove font30 vm ml10 cupo" @click="delRuls(index)"></i>
          </li>
        </ul>-->
      </el-form-item>
      <el-form-item label="" prop="desc">
        <el-input
          type="textarea"
          resize="none"
          style="width:800px;"
          :autosize="{ minRows: 4, maxRows: 6}"
          v-model="formData.desc"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="resource">
        <el-upload
          class="avatar-uploader textRightImgB"
          action="https://luckypay.slsy.com/web/api/upload"
          :show-file-list="false"
          :on-success="sucImgB"
          :before-upload="beforeImgB"
        >
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          <div class="el-upload__text" v-if="!imageUrl">   </div>
          <div
            class="el-upload__tip"
            slot="tip"
          > : 400*150, 2M,  : '.jpg, .gif, .png'.</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="" prop="resource">
        <div class="item_form_wid1000">
          <el-upload
            :limit="4"
            action="https://luckypay.slsy.com/web/api/upload"
            list-type="picture-card"
            :on-success="sucImgS"
            :before-upload="beforeImgS"
            :on-remove="remImgS"
          >
            <i class="el-icon-plus"></i>
            <div class="el-upload__text">   </div>
            <div
              class="el-upload__tip"
              slot="tip"
            >4, (  : 180*100px, 2M,  : '.jpg, .gif, .png'.)</div>
          </el-upload>
        </div>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer ac">
      <el-button type="primary" @click="submitForm('formData')"> </el-button>
      <el-button @click="cancelForm"> </el-button>
    </div>
  </div>
</template>
<script>
export default {
  name: "editUser",
  data() {
    return {
      ruleArr: [{ name1: "", name2: "" }],
      // 
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        region: [
          { required: true, message: "", trigger: "change" }
        ],
        store: [
          { required: true, message: "", trigger: "change" }
        ],
        time: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      },
      // 
      imageUrl: "",
      // 
      dialogImageUrl: "",
      dialogCardList: false
    };
  },
  props: {
    formData: {
      type: Object,
      default: function() {
        return {
          type: "",
          name: "",
          time: "",
          buy: "",
          give: "",
          ruleArr: "",
          type: "",
          resource: "",
          desc: "",
          time: ""
        };
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    typeRadio(e) {
      console.log(e);
    },
    buyRadio(e) {
      console.log(e);
    },
    giveRadio(e) {
      console.log(e);
    },
    addRuls() {
      this.ruleArr.push({ name1: "", name2: "" });
    },
    delRuls(e) {
      if (this.ruleArr.length > 1) {
        this.ruleArr.splice(e, 1);
      }
    },
    // 
    init() {},
    // 
    cascaderChange(val) {
      console.log(val);
    },
    // 
    sucImgB(res, file) {
      console.log(res);
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeImgB(file) {
      console.log(file);
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(" JPG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    },
    // 
    sucImgS() {},
    beforeImgS() {},
    remImgS(file, fileList) {
      console.log(file, fileList);
    },
    // 
    treeClick(e) {
      console.log(this.$refs.tree.getCheckedKeys());
    },

    // 
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.formData);
        console.log(valid);
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });

      this.$emit("submitForm");
    },
    // 
    cancelForm() {
      this.$emit("cancelForm");
    }
  }
};
</script>
<style lang="scss" scoped>
.ruleBox {
  margin-bottom: 10px;
  &:nth-last-of-type(1) {
    margin-bottom: 0;
    i {
      display: inline;
    }
  }
}
</style>




