<template>
  <div class="mainTable">
    <div class="mainTable_title">
      <div>{{tempName}}</div>
    </div>
    <div class="mainTable_search">
      <search-base :searchList="searchList" @searchEvent="searchEvent"></search-base>
    </div>
    <div class="mainTable_content">
      <table-base
        :tableBut="tableBut"
        :tableSelection="tableSelection"
        :tableExpand="tableExpand"
        :tableIndex="tableIndex"
        :tableHead="tableHead"
        :tableData="tableData"
        :tableOperate="tableOperate"
        :tableSize="tableSize"
        :tableTotal="tableTotal"
        @butEvent="butEvent"
        @operateEvent="operateEvent"
        @selectionEvent="selectionEvent"
        @filterEvent="filterEvent"
        @sizeChangeEvent="sizeChangeEvent"
        @currentChangeEvent="currentChangeEvent"
      ></table-base>
    </div>
  </div>
</template>
<script>
export default {
  name: "UserInfo",
  data() {
    return {
      
      tempName: "",
      searchData: {},

      formData: {},
      dialogFormVisible: false,
      tableSelection: false,
      tableExpand: false,
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,

      searchList: [
        {
          id: "input",
          type: "input",
          name: "input",
          title: "",
          placeholder: ""
        },
        {
          id: "input",
          type: "input",
          name: "input",
          title: "",
          placeholder: ""
        },
        {
          id: "daterange",
          type: "timeDate",
          name: "daterange",
          title: "",
          bplaceholder: "",
          eplaceholder: "",
          timeType: "daterange"
        },
        {
          id: "input",
          type: "input",
          name: "input",
          title: "",
          placeholder: ""
        },
        {
          id: "select",
          type: "select",
          name: "select",
          title: "",
          placeholder: ""
        },
        {
          id: "daterange",
          type: "timeDate",
          name: "daterange",
          title: "",
          bplaceholder: "",
          eplaceholder: "",
          timeType: "datetimerange"
        }
      ],

      tableBut: [{ name: " ", value: "export", show: true }],
      tableHead: [
        { name: "", value: "name" },
        { name: "", value: "progress" },
        { name: "", value: "date" },
        { name: "", value: "progress" },
        { name: " ()", value: "progress" },
        {
          name: "",
          value: "status",
          format: (row, column, cellValue, index) => {
            return (cellValue = cellValue == 1 ? "" : "");
          },
          filter: [{ text: "", value: "1" }, { text: "", value: "2" }]
        },
        { name: "", value: "address" },
        { name: "", value: "address" },
        { name: "", value: "date" }
      ],
      tableData: [
        {
          id: 1,
          date: "2016-05-02",
          name: "",
          status: "1",
          address: "",
          progress: 10,
          switch: true,
        },
        {
          id: 2,
          date: "2016-05-04",
          name: "",
          status: "2",
          address: "",
          progress: 30,
          switch: true,
        },
        {
          id: 3,
          date: "2016-05-01",
          name: "",
          status: "1",
          address: " 1519 ",
          progress: 60,
          switch: false
        },
        {
          id: 4,
          date: "2016-05-03",
          name: "",
          status: "1",
          address: " 1516 ",
          progress: 90,
          switch: true,
        },
        {
          id: 5,
          date: "2016-05-03",
          name: "",
          status: "2",
          address: " 1516 ",
          progress: 100,
          switch: false,
        }
      ]
    };
  },
  // 
  components: {
  },
  methods: {
    searchEvent(e) {
      console.log(e);
    },
    butEvent(e) {
      this[e.eventName](e.val);
    },
    filterEvent(e) {
      console.log(e);
    },
    selectionEvent(e) {
      console.log(e);
    },
    operateEvent(e) {
      this[e.eventName](e.val);
    },
    export(e) {
      console.log("export");
      this.$exportData(this.tableHead, this.tableData, this.tempName);
    },
    check(e) {
      this.formData = e || {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: "",
        type: "",
        resource: "",
        desc: ""
      };
      this.dialogFormVisible = true;
    },

    sizeChangeEvent(e) {
      console.log(` ${e} `);
    },
    currentChangeEvent(e) {
      console.log(`: ${e}`);
    },

    cancelForm() {
      this.dialogFormVisible = false;
    },
    submitForm() {
      this.dialogFormVisible = false;
    }
  }
};
</script>
<style lang="scss" scoped>
</style>



