import Vue from 'vue'
import axios from 'axios'
// import router from './router'
// import './plugins/element.js'
import {
  Loading
} from 'element-ui';

// axios.defaults.baseURL = 'https://luckypay.slsy.com/web/api';
axios.defaults.timeout = 5000;
axios.defaults.headers = {
  "X-Requested-With": "XMLHttpRequest"
};

// http request 
axios.interceptors.request.use(
  config => {
    return config
  },
  err => {
    return Promise.reject(err)
  })

// http response 
axios.interceptors.response.use(
  response => {
    // 
    if (response.data.code == 20000) {
      return response
    }
    // 
    else if (response.data.code == 40001) {
      return Vue.prototype.$message.error({
        title: '',
        duration: 2500,
        message: response.data.msg
      });
    }
    // 
    else if (response.data.code == 1002) {
      return Vue.prototype.$notify.error({
        title: '',
        duration: 2500,
        message: response.data.desc
      });
    }
    // 
    else if (response.data.code == 1003) {
      //
      // return router.push('/login');
    }
    // 
    else if (response.data.code == 1004) {
      // 
      return Vue.prototype.$notify.error({
        title: '',
        duration: 2500,
        message: response.data.desc
      });
    }
    // 
    else if (response.data.code == 1009) {
      return Vue.prototype.$notify.error({
        title: '',
        duration: 2500,
        message: response.data.desc
      });
    }

  },
  err => {
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          console.log('')
          break;
        case 401:
          console.log('')
          break;
        case 403:
          console.log('')
          break;
        case 404:
          console.log(',')
          break;
        case 405:
          console.log('')
          break;
        case 408:
          console.log('')
          break;
        case 500:
          console.log('')
          break;
        case 501:
          console.log('')
          break;
        case 502:
          console.log('')
          break;
        case 503:
          console.log('')
          break;
        case 504:
          console.log('')
          break;
        case 505:
          console.log('http')
          break;
        default:
          console.log(`${err.response.status}`)
      }
    } else {
      console.log('')
    }
    return Promise.resolve(err.response)
    return Promise.reject(err)
  })

const req = (method, url, params) => {
  return axios({
    method: method,
    url: url,
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
      token: localStorage.getItem('logintoken')
    },
    data: params,
    traditional: true,
    transformRequest: [
      function (data) {
        let ret = ''
        for (let it in data) {
          ret +=
            encodeURIComponent(it) +
            '=' +
            encodeURIComponent(data[it]) +
            '&'
        }
        return ret
      }
    ]
  }).then(res => res.data);
};

export default {
  get: function (url, params, config) {
    return req("GET", url, params, config);
  },

  post: function (url, params, config) {
    return req("POST", url, params, config);
  },

  delete: function (url, params, config) {
    return req("DELETE", url, params, config);
  },

  put: function (url, params, config) {
    return req("PUT", url, params, config);
  }
};



