// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.


import App from './App'
import Vue from 'vue'
import routes from './routes'
import VueRouter from 'vue-router'
import moment from 'moment'
Vue.use(VueRouter)
import ElementUI from 'element-ui';
Vue.use(ElementUI)
import 'element-ui/lib/theme-chalk/index.css';
import './assets/css/index.scss';



// 
import store from './vuex/store.js';
// icon
import './assets/icon/iconfont.css'

// echarts
import echarts from 'echarts'
Vue.prototype.$echarts = echarts

// import axios from 'axios';
// Vue.prototype.$axios = axios;

Vue.config.productionTip = false

// excel
import util from './utils/util';
Vue.prototype.$exportData = util.exportExcel;
// Vue.prototype.$importData = util.importExcel;

// 
import * as custom from './utils/util'
Object.keys(custom).forEach(key => {
  Vue.filter(key, custom[key])
})


Vue.filter('datefmt', (input, fmtstring) => {
  return moment(input).format(fmtstring)
})

// 
const router = new VueRouter({
  routes
})


// 
router.beforeEach((to, from, next) => {
  if (to.matched.length != 0) {
    if (to.meta.requireAuth) { // 
      if (Boolean(localStorage.getItem("userInfo"))) { // vuex stateuser
        next();
      } else {
        next({
          path: '/login',
          query: {
            redirect: to.fullPath
          } // path
        })
      }
    } else {
      if (Boolean(localStorage.getItem("userInfo"))) { // 
        if (to.path != "/" && to.path != "/login") { //
          next();
        } else {
          /**
           *  
           */
          next({
            path: '/goods/Goods'
          })
        }
      } else {
        next();
      }
    }
  } else {
    next({
      path: '/login',
      query: {
        redirect: to.fullPath
      } // path
    })
  }
})


// 
import componentLibrary from './components/components';
Vue.use(componentLibrary);


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store, //store vuex
  components: {
    App
  },
  template: '<App/>',
  render: h => h(App),
  data: {
    // 
    Bus: new Vue()
  }

})



