<template>
  <div class="mainTable">
    <div class="mainTable_title">
      <div></div>
    </div>
    <div class="mainTable_swith">
      <el-row>
        <el-col :span="20">
          <div>
             :  ,  , 
            <span class="colBlue">( 10010 )</span> ,  ,  .
          </div>
        </el-col>
        <el-col :span="4">
          <div class="fr txt">
            <el-switch v-model="switchData.delivery"></el-switch>
          </div>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="20">
          <div> :  ,  .  ,  .</div>
        </el-col>
        <el-col :span="4">
          <div class="fr txt">
            <el-switch v-model="switchData.delivery"></el-switch>
          </div>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="20">
          <div> :  ,  -  ,  .</div>
        </el-col>
        <el-col :span="4">
          <div class="fr txt">
            <el-switch v-model="switchData.delivery"></el-switch>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="mainTable_other">
      <el-row>
        <el-col :span="12">
          <div class="title">
            
            <span>
              (  , 
              <label for></label> . )
            </span>
          </div>
          <div class="select"> : &nbsp;
            <el-select v-model="formData.region" placeholder="" clearable>
              <el-option label="" value="1"></el-option>
              <el-option label=" 30 " value="2"></el-option>
              <el-option label=" 90 " value="3"></el-option>
              <el-option label=" 180 " value="4"></el-option>
              <el-option label=" 1 " value="5"></el-option>
              <el-option label=" 2 " value="6"></el-option>
              <el-option label=" 3 " value="7"></el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="title">
            
            <span>
              (  , 
              <label for>6</label>  . )
            </span>
          </div>
          <div class="money">
            <ul>
              <li v-for="(item, index) in moneyList" :key="index">
                <i class="el-icon-circle-close-outline" @click="delMoney(index)"></i>
                {{item}}
              </li>
            </ul>
            <el-button class="btn" type="primary" icon="el-icon-plus" @click="addMoney"></el-button>
          </div>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <div class="title">
            
            <span>(  ,  4  ,  ,  : 750*300 , 2M ,  : '.jpg , .gif , .png' . )</span>
          </div>
          <div class="upDataImg">
            <el-upload
              :limit="4"
              action="https://luckypay.slsy.com/web/api/upload"
              list-type="picture-card"
              :on-success="sucImg"
              :on-remove="remImg"
            >
              <i class="el-icon-plus"></i>
              <div class="el-upload__text">   </div>
            </el-upload>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="mainTable_content">
      <el-row>
        <el-col :span="24">
          <div class="title">
            
            <span>(  ,  ,  ,  .)</span>
          </div>
        </el-col>
      </el-row>
      <table-base
        :tableBut="tableBut"
        :tableSelection="tableSelection"
        :tableExpand="tableExpand"
        :tableIndex="tableIndex"
        :tableHead="tableHead"
        :tableData="tableData"
        :tableOperate="tableOperate"
        :tableSize="tableSize"
        :tableTotal="tableTotal"
        @butEvent="butEvent"
        @operateEvent="operateEvent"
        @switchEvent="switchEvent"
        @sortEvent="sortEvent"
        @selectionEvent="selectionEvent"
        @filterEvent="filterEvent"
        @sizeChangeEvent="sizeChangeEvent"
        @currentChangeEvent="currentChangeEvent"
      ></table-base>
    </div>
    <div class="mainTable_dialog">
      <el-dialog
        width="1000px"
        title=""
        top="10vh"
        v-if="dialogFormVisible"
        :visible.sync="dialogFormVisible"
        :close-on-click-modal="false"
      >
        <config-edit :formData="formData" @cancelForm="cancelForm" @click="submitForm"></config-edit>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import configEdit from "./configEdit.vue";
export default {
  name: "CardConfig",
  data() {
    return {
      moneyList: [100, 200, 500, 800],
      switchData: {},
      formData: {},
      dialogFormVisible: false,
      tableSelection: false,
      tableExpand: false,
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,

      tableBut: [{ name: " ", value: "edit", show: true }],
      tableHead: [
        { name: "", value: "name" },
        { name: "", value: "id" },
        { name: "", value: "date2", sort: true },
        { name: "", value: "date", sort: true },
        { name: "", value: "switch", switch: true },
        {
          name: "",
          value: "status",
          format: (row, column, cellValue, index) => {
            return (cellValue = cellValue == 1 ? "" : "");
          },
          filter: [
            { text: "", value: "1" },
            { text: "", value: "2" }
          ]
        }
      ],
      tableData: [
        {
          id: 1,
          date2: "2016-05-02",
          date: "2016-05-02",
          name: "",
          status: "1",
          switch: true
        },
        {
          id: 2,
          date2: "2016-05-02",
          date: "2016-05-04",
          name: "",
          status: "2",
          progress: 30,
          switch: true
        },
        {
          id: 3,
          date2: "2016-05-02",
          date: "2016-05-01",
          name: "",
          status: "1",
          progress: 60,
          switch: false
        },
        {
          id: 4,
          date2: "2016-05-02",
          date: "2016-05-03",
          name: "",
          status: "1",
          progress: 90,
          switch: true
        },
        {
          id: 5,
          date2: "2016-05-02",
          date: "2016-05-03",
          name: "",
          status: "2",
          progress: 100,
          switch: false
        }
      ],
      tableOperate: [
        {
          name: "",
          value: "open",
          show: function(e) {
            return e.status != 1 ? true : false;
          }
        },
        {
          name: "",
          value: "close",
          show: function(e) {
            return e.status == 1 ? true : false;
          }
        },
        {
          name: "",
          value: "edit",
          show: function(e) {
            return true;
          }
        },
        {
          name: "",
          value: "delete",
          show: function(e) {
            return true;
          }
        }
      ]
    };
  },
  // 
  components: {
    configEdit
  },
  methods: {
    butEvent(e) {
      this[e.eventName](e.val);
    },
    filterEvent(e) {
      console.log(e);
    },
    selectionEvent(e) {
      console.log(e);
    },
    sortEvent(e) {
      console.log(e);
    },
    switchEvent(e) {
      console.log(e.name);
    },
    operateEvent(e) {
      this[e.eventName](e.val);
    },
    import(e) {
      console.log("import");
    },
    export(e) {
      console.log("export");
    },
    open(e) {
      console.log(e.name);
    },
    close(e) {
      console.log(e.name);
    },
    edit(e) {
      this.formData = e || {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: "",
        type: "",
        resource: "",
        desc: ""
      };
      this.dialogFormVisible = true;
    },
    delete(e) {
      if (e) {
        console.log(e.name);
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            this.$message({
              type: "success",
              message: "!"
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      } else {
        console.log("");
      }
    },
    check(e) {
      console.log(e.name);
    },

    sizeChangeEvent(e) {
      console.log(` ${e} `);
    },
    currentChangeEvent(e) {
      console.log(`: ${e}`);
    },

    addMoney() {
      this.$prompt(" !", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        // inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        inputErrorMessage: "",
        inputPlaceholder: ""
      })
        .then(({ value }) => {
          this.$message({
            type: "success",
            message: ": " + value
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    delMoney(e) {
      this.moneyList.splice(e, 1);
    },
    sucImg(e) {
      console.log(e);
    },
    remImg(e) {
      console.log(e);
    },
    cancelForm() {
      this.dialogFormVisible = false;
    },
    submitForm() {
      this.dialogFormVisible = false;
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../../assets/css/varible.scss";

.el-row {
  line-height: 40px;
  .colBlue {
    color: $hoverColor;
  }
  .title {
    font-weight: bold;
    padding-left: 15px;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      width: 3px;
      height: 16px;
      top: 12px;
      left: 3px;
      background: $hoverColor;
    }
    span {
      font-size: 14px;
      color: $fontColor666;
      label {
        color: $fontColorRed;
      }
    }
  }
}
.mainTable_swith {
  .txt {
    font-size: 15px;
    font-weight: bold;
    .el-switch {
      margin-left: 20px;
    }
  }
}
.mainTable_other {
  margin-top: 10px;
  .title {
    font-weight: bold;
    padding-left: 15px;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      width: 3px;
      height: 16px;
      top: 12px;
      left: 3px;
      background: $hoverColor;
    }
    span {
      font-size: 14px;
      color: $fontColor666;
      label {
        color: $fontColorRed;
      }
    }
  }
  .select {
    padding-left: 20px;
    margin: 10px 0;
  }
  .money {
    padding-left: 20px;
    margin: 10px 0;
    ul {
      li {
        text-align: center;
        border: 1px solid #dcdfe6;
        border-radius: 4px;
        float: left;
        width: 80px;
        height: 40px;
        position: relative;
        margin-right: 20px;
        line-height: 40px;
        position: relative;
        i {
          color: $hoverColor;
          position: absolute;
          top: 2px;
          right: 2px;
          cursor: pointer;
          display: none;
        }
        &:hover {
          border: 1px solid $hoverColor;
          box-shadow: 0 2px 12px 0 rgba(57, 130, 209, 0.2) !important;
          i {
            display: inline;
          }
        }
      }
    }
    .btn {
      font-size: 20px;
      padding: 9px 20px;
    }
  }
  .upDataImg {
    padding-left: 20px;
    margin: 10px 0;
  }
}
</style>




