<template>
  <div class="dialogBox sizeInline">
    <el-form
      inline
      :model="formData"
      :rules="rules"
      size="medium"
      ref="formData"
      label-width="85px"
      label-position="right"
    >
      <el-form-item label="" prop="name">
        <el-input v-model="formData.name" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-radio-group v-model="formData.type" @change="typeRadio" style="min-width:330px">
          <el-radio label="money"></el-radio>
          <el-radio label="card"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-radio-group v-model="formData.type" @change="typeRadio" style="min-width:330px">
          <el-radio label="money"></el-radio>
          <el-radio label="card">/</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-radio-group v-model="formData.type" @change="typeRadio" style="min-width:330px">
          <el-radio label="money"></el-radio>
          <el-radio label="card"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-radio-group v-model="formData.type" @change="typeRadio">
          <el-radio label="money"> (100, 5)</el-radio>
          <el-radio label="card"> (100, 5)</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="name">
        <ul>
          <li class="ruleBox" v-for="(item, index) in ruleArr" :key="index">
            
            <el-input class="wid108 ac" v-model="item.name1" placeholder="" clearable></el-input>&nbsp;&nbsp;,
            &nbsp;&nbsp;
            <el-input class="wid108 ac" v-model="item.name2" placeholder="" clearable></el-input>&nbsp;
            <!-- <span></span> -->
            <span>%</span>,&nbsp;&nbsp;
            <el-input class="wid108 ac" v-model="item.name2" placeholder="" clearable></el-input>&nbsp;
            <!-- <span></span> -->
            <span>%</span>,&nbsp;&nbsp;
            <el-input class="wid108 ac" v-model="item.name2" placeholder="" clearable></el-input>&nbsp;
            <!-- <span></span> -->
            <span>%</span>
            <i class="el-icon-circle-plus font30 vm ml10 cupo hide" @click="addRuls"></i>
            <i class="el-icon-remove font30 vm ml10 cupo" @click="delRuls(index)"></i>
          </li>
        </ul>
      </el-form-item>
      <el-form-item label="" prop="desc">
        <div class="treeBox" style="width:330px;">
          <el-tree
            show-checkbox
            ref="tree"
            :data="storeOptions"
            node-key="id"
            accordion
            @check-change="treeClick"
          ></el-tree>
        </div>
      </el-form-item>
      <el-form-item label="" prop="desc">
        <el-input
          type="textarea"
          resize="none"
          style="width:330px;"
          :autosize="{ minRows: 4, maxRows: 6}"
          v-model="formData.desc"
        ></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer ac">
      <el-button type="primary" @click="submitForm('formData')"> </el-button>
      <el-button @click="cancelForm"> </el-button>
    </div>
  </div>
</template>
<script>
export default {
  name: "editUser",
  data() {
    return {
      ruleArr: [{ name1: "", name2: "" }],
      // 
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        region: [
          { required: true, message: "", trigger: "change" }
        ],
        store: [
          { required: true, message: "", trigger: "change" }
        ],
        time: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      },
      // 
      imageUrl: "",
      // 
      dialogImageUrl: "",
      dialogCardList: false,

      // 
      storeOptions: [
        {
          id: "1",
          value: "zhinan",
          label: "",
          children: [
            {
              id: "2",
              value: "shejiyuanze",
              label: "",
              children: [
                { id: "3", value: "yizhi", label: "" },
                { id: "4", value: "fankui", label: "" },
                { id: "5", value: "xiaolv", label: "" },
                { id: "6", value: "kekong", label: "" }
              ]
            },
            {
              id: "7",
              value: "daohang",
              label: "",
              children: [
                { id: "8", value: "cexiangdaohang", label: "" },
                { id: "9", value: "dingbudaohang", label: "" }
              ]
            }
          ]
        },
        {
          id: "10",
          value: "zujian",
          label: "",
          children: [
            {
              id: "11",
              value: "basic",
              label: "Basic",
              children: [
                { id: "12", value: "layout", label: "Layout " },
                { id: "13", value: "color", label: "Color " },
                { id: "14", value: "typography", label: "Typography " },
                { id: "15", value: "icon", label: "Icon " },
                { id: "16", value: "button", label: "Button " }
              ]
            },
            {
              id: "17",
              value: "form",
              label: "Form",
              children: [
                { id: "18", value: "radio", label: "Radio " },
                { id: "19", value: "checkbox", label: "Checkbox " },
                { id: "20", value: "input", label: "Input " },
                {
                  id: "21",
                  value: "input-number",
                  label: "InputNumber "
                },
                { id: "22", value: "select", label: "Select " },
                { id: "23", value: "cascader", label: "Cascader " },
                { id: "24", value: "switch", label: "Switch " },
                { id: "25", value: "slider", label: "Slider " },
                {
                  id: "26",
                  value: "time-picker",
                  label: "TimePicker "
                },
                {
                  id: "27",
                  value: "date-picker",
                  label: "DatePicker "
                },
                {
                  id: "28",
                  value: "datetime-picker",
                  label: "DateTimePicker "
                },
                { id: "29", value: "upload", label: "Upload " },
                { id: "30", value: "rate", label: "Rate " },
                { id: "31", value: "form", label: "Form " }
              ]
            },
            {
              id: "32",
              value: "data",
              label: "Data",
              children: [
                { id: "33", value: "table", label: "Table " },
                { id: "34", value: "tag", label: "Tag " },
                { id: "35", value: "progress", label: "Progress " },
                { id: "36", value: "tree", label: "Tree " },
                { id: "37", value: "pagination", label: "Pagination " },
                { id: "38", value: "badge", label: "Badge " }
              ]
            },
            {
              id: "39",
              value: "notice",
              label: "Notice",
              children: [
                { id: "40", value: "alert", label: "Alert " },
                { id: "41", value: "loading", label: "Loading " },
                { id: "42", value: "message", label: "Message " },
                { id: "43", value: "message-box", label: "MessageBox " },
                { id: "44", value: "notification", label: "Notification " }
              ]
            },
            {
              id: "45",
              value: "navigation",
              label: "Navigation",
              children: [
                { id: "46", value: "menu", label: "NavMenu " },
                { id: "47", value: "tabs", label: "Tabs " },
                { id: "48", value: "breadcrumb", label: "Breadcrumb " },
                { id: "49", value: "dropdown", label: "Dropdown " },
                { id: "50", value: "steps", label: "Steps " }
              ]
            },
            {
              id: "51",
              value: "others",
              label: "Others",
              children: [
                { id: "52", value: "dialog", label: "Dialog " },
                { id: "53", value: "tooltip", label: "Tooltip " },
                { id: "54", value: "popover", label: "Popover " },
                { id: "55", value: "card", label: "Card " },
                { id: "56", value: "carousel", label: "Carousel " },
                { id: "57", value: "collapse", label: "Collapse " }
              ]
            }
          ]
        },
        {
          id: "58",
          value: "ziyuan",
          label: "",
          children: [
            { id: "59", value: "axure", label: "Axure Components" },
            { id: "60", value: "sketch", label: "Sketch Templates" },
            { id: "61", value: "jiaohu", label: "" }
          ]
        }
      ]
    };
  },
  props: {
    formData: {
      type: Object,
      default: function() {
        return {
          type: "",
          name: "",
          time: "",
          buy: "",
          give: "",
          ruleArr: "",
          type: "",
          resource: "",
          desc: "",
          time: ""
        };
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    typeRadio(e) {
      console.log(e);
    },
    buyRadio(e) {
      console.log(e);
    },
    giveRadio(e) {
      console.log(e);
    },
    addRuls() {
      this.ruleArr.push({ name1: "", name2: "" });
    },
    delRuls(e) {
      if (this.ruleArr.length > 1) {
        this.ruleArr.splice(e, 1);
      }
    },
    // 
    init() {},
    // 
    cascaderChange(val) {
      console.log(val);
    },
    // 
    sucImgB(res, file) {
      console.log(res);
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeImgB(file) {
      console.log(file);
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(" JPG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    },
    // 
    sucImgS() {},
    beforeImgS() {},
    remImgS(file, fileList) {
      console.log(file, fileList);
    },
    // 
    treeClick(e) {
      console.log(this.$refs.tree.getCheckedKeys());
    },

    // 
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.formData);
        console.log(valid);
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });

      this.$emit("submitForm");
    },
    // 
    cancelForm() {
      this.$emit("cancelForm");
    }
  }
};
</script>
<style lang="scss" scoped>
.ruleBox {
  margin-bottom: 10px;
  &:nth-last-of-type(1) {
    margin-bottom: 0;
    i {
      display: inline;
    }
  }
}
</style>




