<template>
  <div class="dialogBox">
    <div class="titleText">
       :
      <span>0</span>  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
       :
      <span>0</span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
       :
      <span>0</span> 
    </div>
    <div class="mainTable_content">
      <table-base
        :tableIndex="tableIndex"
        :tableHead="tableHead"
        :tableData="tableData"
        :tableSize="tableSize"
        :tableTotal="tableTotal"
        :tableOperate="tableOperate"
        @sizeChangeEvent="sizeChangeEvent"
        @currentChangeEvent="currentChangeEvent"
      ></table-base>
    </div>
  </div>
</template>
<script>
export default {
  name: "acCheck",
  data() {
    return {
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,
      tableHead: [
        { name: "", value: "name" },
        { name: " ()", value: "date" },
        { name: " ()", value: "address" },
        { name: "", value: "progress" },
        { name: "", value: "id" }
      ],
      tableData: [
        {
          id: 1,
          date: "2016-05-02",
          name: "",
          status: "1",
          address: " 1518 ",
          progress: 10
        },
        {
          id: 2,
          date: "2016-05-04",
          name: "",
          status: "2",
          address: " 1517 ",
          progress: 30
        },
        {
          id: 3,
          date: "2016-05-01",
          name: "",
          status: "1",
          address: " 1519 ",
          progress: 60
        },
        {
          id: 4,
          date: "2016-05-03",
          name: "",
          status: "1",
          address: "",
          progress: 90
        },
        {
          id: 5,
          date: "2016-05-03",
          name: "",
          status: "2",
          address: "",
          progress: 100
        }
      ],
      tableOperate: [
        {
          name: "",
          value: "forbidden",
          show: function(e) {
            return true;
          }
        },
        {
          name: "",
          value: "check",
          show: function(e) {
            return true;
          }
        }
      ]
    };
  },
  // 
  components: {
  },
  props: {
    formData: {
      type: Object,
      default: function() {
        return {
          type: "",
          name: "",
          time: "",
          buy: "",
          give: "",
          ruleArr: "",
          type: "",
          resource: "",
          desc: "",
          time: ""
        };
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    // 
    init() {
      console.log(this.formData);
    },

    sizeChangeEvent(e) {
      console.log(` ${e} `);
    },
    currentChangeEvent(e) {
      console.log(`: ${e}`);
    }
  }
};
</script>
<style lang="scss" scoped>
.titleText {
  font-size: 18px;
  font-weight: bold;
}
</style>




