<template>
  <div class="searchBox">
    <el-row :gutter="0" class="serBox">
      <div v-for="item in searchList" :key="item.value">
        <!--  -->
        <el-col :span="6" v-if="item.type=='input'">
          <label :for="item.id">{{item.title}} :</label>
          <el-input
            :id="item.id"
            size="medium"
            v-model="searchData[item.name]"
            :placeholder="item.placeholder"
            clearable
          ></el-input>
        </el-col>
        <!--  -->
        <el-col :span="6" v-if="item.type=='select'">
          <label :for="item.id">{{item.title}} :</label>
          <el-select
            :id="item.id"
            size="medium"
            v-model="searchData[item.name]"
            :placeholder="item.placeholder"
            clearable
          >
            <el-option
              v-for="opt in item.options"
              :key="opt.value"
              :label="opt.name"
              :value="opt.value"
            ></el-option>
          </el-select>
        </el-col>
        <!--  -->
        <el-col :span="8" v-if="item.type=='cascader'">
          <label :for="item.id">{{item.title}} :</label>
          <el-cascader
            :id="item.id"
            size="medium"
            change-on-select
            v-model="searchData[item.name]"
            :options="cascaderOptions"
            :placeholder="item.placeholder"
            @active-item-change="cascaderChange"
            clearable
          ></el-cascader>
        </el-col>
        <!--  -->
        <el-col :span="8" v-if="item.type=='time'">
          <label :for="item.id">{{item.title}} :</label>
          <el-time-picker
            :id="item.id"
            size="medium"
            is-range
            v-model="searchData[item.name]"
            range-separator=""
            :start-placeholder="item.bplaceholder"
            :end-placeholder="item.eplaceholder"
            placeholder=""
          ></el-time-picker>
        </el-col>
        <!--  -->
        <!-- year/month/date/week/datetime/datetimerange/daterange -->
        <el-col :span="6" v-if="item.type=='timeDateSim'">
          <label :for="item.id">{{item.title}} :</label>
          <el-date-picker
            v-model="searchData[item.name]"
            :id="item.id"
            size="medium"
            :placeholder="item.placeholder"
            :type="item.timeType"
            clearable
          ></el-date-picker>
        </el-col>
        <el-col :span="9" v-if="item.type=='timeDate'">
          <label :for="item.id">{{item.title}} :</label>
          <el-date-picker
            align="right"
            range-separator=""
            v-model="searchData[item.name]"
            :id="item.id"
            size="medium"
            :type="item.timeType"
            :start-placeholder="item.bplaceholder"
            :end-placeholder="item.eplaceholder"
            clearable
          ></el-date-picker>
        </el-col>
      </div>
      <el-button
        class="butNormal serBut"
        type="primary"
        size="medium"
        @click="searchEvent"
        icon="el-icon-search"
      > </el-button>
    </el-row>
  </div>
</template>
<script>
export default {
  name: "searchbase",
  data() {
    return {
      // 
      cascaderOptions: [
        {
          id: "1",
          value: "zhinan",
          label: "",
          children: [
            {
              id: "2",
              value: "shejiyuanze",
              label: "",
              children: [
                { id: "3", value: "yizhi", label: "" },
                { id: "4", value: "fankui", label: "" },
                { id: "5", value: "xiaolv", label: "" },
                { id: "6", value: "kekong", label: "" }
              ]
            },
            {
              id: "7",
              value: "daohang",
              label: "",
              children: [
                { id: "8", value: "cexiangdaohang", label: "" },
                { id: "9", value: "dingbudaohang", label: "" }
              ]
            }
          ]
        },
        {
          id: "10",
          value: "zujian",
          label: "",
          children: [
            {
              id: "11",
              value: "basic",
              label: "Basic",
              children: [
                { id: "12", value: "layout", label: "Layout " },
                { id: "13", value: "color", label: "Color " },
                { id: "14", value: "typography", label: "Typography " },
                { id: "15", value: "icon", label: "Icon " },
                { id: "16", value: "button", label: "Button " }
              ]
            },
            {
              id: "17",
              value: "form",
              label: "Form",
              children: [
                { id: "18", value: "radio", label: "Radio " },
                { id: "19", value: "checkbox", label: "Checkbox " },
                { id: "20", value: "input", label: "Input " },
                {
                  id: "21",
                  value: "input-number",
                  label: "InputNumber "
                },
                { id: "22", value: "select", label: "Select " },
                { id: "23", value: "cascader", label: "Cascader " },
                { id: "24", value: "switch", label: "Switch " },
                { id: "25", value: "slider", label: "Slider " },
                {
                  id: "26",
                  value: "time-picker",
                  label: "TimePicker "
                },
                {
                  id: "27",
                  value: "date-picker",
                  label: "DatePicker "
                },
                {
                  id: "28",
                  value: "datetime-picker",
                  label: "DateTimePicker "
                },
                { id: "29", value: "upload", label: "Upload " },
                { id: "30", value: "rate", label: "Rate " },
                { id: "31", value: "form", label: "Form " }
              ]
            },
            {
